@import "../../variables";

.Loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40rem;
    height: 40rem;
    z-index: 1;

    @media only screen and (max-width: $bp-medium) {
        width: 20rem;
        height: 20rem;
    }
}