@import "../../variables";

.Timer {
    position: absolute;
    top: 2rem;
    right: 2rem;
    font-size: $default-font-size;
    font-weight: 700;

    display: flex;
    flex-direction: row;
    align-items: center;

    &__Icon {
        width: 2rem;
        height: 2rem;
        margin-right: 1rem;
        animation: pulse 1s infinite;
    }
}

.Blinking {
    animation: blinking 1s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.3);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes blinking {
    0% {
        color: $color-black;
        fill: $color-black;
    }

    50% {
        color: $color-primary-dark;
        fill: $color-primary-dark;
    }

    100% {
        color: $color-black;
        fill: $color-black;
    }
}