@import "../../variables";

.StartQuiz {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40rem;
    height: 22rem;

    @media only screen and (max-width: $bp-xsmall) {
        top: 80%;
        width: 100%;
    }


    &__Form {
        height: 100%;
        background-color: $color-white;
        
        box-shadow: $shadow-dark;

        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;

        button {
            margin-top: auto;
        }

        
    }
}