@import "../../../variables";

.SelectInputWrapper {
    display: inline-block;
    position: relative;
    font-size: $default-font-size;

    label {
        display: inline-block;
        color: rgba($color-grey-dark, 0.8);
        margin-right: 1rem;
        padding: 0.5rem;
    }

    &:not(:last-child) {
        margin-bottom: 2rem;
    }
}

.SelectInput {
    display: inline-block;
    min-width: 24rem;
    padding: 0.5rem;
    border-bottom: 1px solid rgba($color-grey-dark, 0.5);
    position: relative;
    cursor: pointer;
    transition: all 0.2s ease-out;

    &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(-50%, -50%);
        width: 1rem;
        height: 1rem;
        background-color: $color-grey-dark;
        clip-path: polygon(50% 100%, 0 30%, 100% 30%);
        transition: all 0.2s ease-out;
    }

    &:hover {
        border-bottom: 1px solid rgba($color-primary-light, 0.5);
    }

    &:hover::after { 
        background-color: $color-primary-light;
    }
}

.SelectInput__OptionsShown {
    border-bottom: 1px solid rgba($color-primary-light, 0.5);

    &::after {
        background-color: $color-primary-light;
    }
}