@import "../../../variables";

.Button {
    all: unset;
    cursor: pointer;
    box-sizing: inherit;
    display: inline-block;
    font-size: $default-font-size;
    text-transform: uppercase;
    font-weight: 600;
    padding: 1rem 2rem;
    background-color: $color-primary;
    color: $color-white;
    transition: all 0.2s ease-out;

    &:hover {
        background-color: $color-tertiary-dark;
    }

    &:disabled {
        background-color: $color-grey-dark;
        cursor: not-allowed;
    }
}