@import "../../../variables";

.Char {
    display: inline-block;
    font-size: 8rem;
    font-weight: 600;
    text-transform: uppercase;
    padding: 1.5rem;
    line-height: 1;
    color: $color-white;
    text-shadow: 0.3rem 0.3rem 0.9rem rgba($color-black, 0.4);
    position: relative;
    z-index: 100;

    @media only screen and (max-width: $bp-small) {
        font-size: 6rem;
    }

    @media only screen and (max-width: $bp-xsmall) {
        font-size: 4rem;
    }

    &:not(:last-child) {
        margin-right: 1rem;
    }

    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &::before {
        transform: rotateZ(-10deg + random(5)*1deg);
        z-index: -1;
    }

    &::after {
        background-color: $title-card-after;
        transform: rotateZ(random(10)*1deg);
        width: calc(100% + 4px);
        border: 2px solid $color-white;
        z-index: -10;
    }

    @for $i from 1 through 6 {
        &__#{$i} {
            &::before {
                background-color: nth($colors, $i);
            }
        }
    }
}