@import "../../variables";

.Question {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $color-white;
    box-shadow: $shadow-dark;
    display: flex;
    flex-direction: column;
    padding: 6rem 2rem 2rem 2rem;
    width: 90%;
    max-width: 90rem;
    height: 40rem;
    font-size: $default-font-size;

    &__Joker {
        position: absolute;
        top: 1rem;
        left: 50%;
        transform: translate(-50%, 0);
    }

    &__Number {
        position: absolute;
        top: 0;
        left: 0;
        font-weight: 700;
        font-size: 10rem;
        line-height: 0.6;
        letter-spacing: -0.135em;
        color: rgba($color-primary-light, 0.2);
        z-index: -1;
    }

    &__Text {
        padding: 2rem 0;
        font-weight: 700;
        font-size: 2rem;
    }

    &__Choices {
        margin-top: 2rem;
    }

    button {
        margin: auto 0 0 auto;
    }
}

.ButtonText {
    display: flex;
    align-items: center;

    &__Icon {
        fill: $color-white;
        width: 1.8rem;
        height: 1.8rem;
        margin-right: 5px;
    }
}